import './hero.css';
import React from 'react'
import {BsBag,BsArrowBarRight} from 'react-icons/bs'
import { heroimg } from '../Exportimage';
import {CgArrowRight} from 'react-icons/cg';
import {motion} from 'framer-motion'
const Hero = () => {
  const transition = {
    duration: 3,
    type: "spring",
  };
  return (
    <div className='hero'>
      <div className="container">
        <div className="left-hero">
          <div>
            <span>skin</span>
            <span>protection</span>
            <span>cream</span>
          </div>
          <div>
            <h2>
              <span>Trendy</span>
              <span>Collection</span>
            </h2>
            <p>Seedily say has suitable disposal and boy. 
              Exercise joy man children rejoiced.</p>
          </div>
        </div>
        <div className="center-hero">
          <motion.div
          initial={{y:'-80px'}}
          whileInView={{y:'0px'}}
          transition={transition}
          className="background"></motion.div>
          <motion.img
          initial={{y:'20px'}}
          whileInView={{y:'0px'}}
          transition={transition}
          src={heroimg} alt="" className='fashonista'/>
          <div className="center-text">
            <div            
            className="bag">
              <BsBag/>
            </div>
            <div className="best">
              <p>
                <span>best signup</span>
                <span>offers</span>
              </p>
              <CgArrowRight/>
            </div>
          </div>
        </div>
        <div className="right-hero">
          <div>
            <span>1.5m</span>
            <span>Monthly traffic</span>
          </div>
          <div>
            <span>100k</span>
            <span>happy customers</span>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Hero