import './header.css';
import React from 'react'
import { logo } from '../Exportimage';
import {CgShoppingBag} from 'react-icons/cg'
import {Link} from 'react-router-dom'
import { useState } from 'react';
import {BiSidebar} from 'react-icons/bi'
const Header = () => {
  const mobile=window.innerWidth<=767 ? true : false;
  const [opened,setopned]=useState(false);
  return (
    <div className='header'>
      <div className="container">
        <Link to={'/'} className="logo">
          <img src={logo} alt="" />
          <span>osha market</span>
        </Link>
        <div className="header-right">
          <div className="menu">
            {
               opened===false && mobile===true ? (
                <BiSidebar
                onClick={()=>{
                  setopned(true)
                }}
                />
              ):(
                <ul>
                <li>
                  <Link to={'/'}
                    onClick={()=>{setopned(false)}}
                  >
                    home
                  </Link>
                </li>
                <li>
                  <Link to={'/'}
                  onClick={()=>{setopned(false)}}>
                    brand
                  </Link>
                </li>                            <li>
                  <Link to={'/'}
                  onClick={()=>{setopned(false)}}
                  >
                    new
                  </Link>
                </li>
                <li>
                  <Link to={'/'}
                  onClick={()=>{setopned(false)}}
                  >
                    sales
                  </Link>
                </li>
                <li>
                  <Link to={'/'}
                  onClick={()=>{setopned(false)}}
                  >
                    eng
                  </Link>
                </li>
                <li>
                  <Link to={'/cart'}
                  onClick={()=>{setopned(false)}}
                  >
                    <CgShoppingBag/>
                  </Link>
                </li>
              </ul>
              )
            }
          </div>
          <input type="text" placeholder='search' className='search'/>
        </div>
      </div>
    </div>
  )
}

export default Header