import './skinproduction.css';
import {useDispatch} from 'react-redux'
import {ProductsData} from '../data/Productsdata';
import { addProduct } from '../rtk/Slices/ProductSlice';
import React from 'react'
import {Swiper,SwiperSlide} from 'swiper/react'
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Pagination, Navigation } from "swiper";
const Skinproduction = () => {
  const dispatch=useDispatch();
  return (
    <div className='skinproduction'>
      <div className="skinproduction-content">
      <Swiper
        breakpoints={
          {
            0:{
              slidesPerView:1,
              spaceBetween:10
            },
            480:{
              slidesPerView:1,
              spaceBetween:10
            },
            768:{
              slidesPerView:3,
              spaceBetween:15
            },
            1024:{
              slidesPerView:3,
              spaceBetween:15
            },
            1280:{
              slidesPerView:3,
              spaceBetween:25
            }
          }
        }
            slidesPerView={3}
            spaceBetween={40}
            slidesPerGroup={1}
            loop={true}
            loopFillGroupWithBlank={true}
/*             pagination={{
              clickable: true,
            }} */
            navigation={true}
            modules={[Pagination, Navigation]}
            className="mySwiper"
      >
        {
          ProductsData.map((skinproduct,index)=>{
            return(
              <SwiperSlide key={index} className='skin-slide'>
                <h3>{skinproduct.name}</h3>
                <p>{skinproduct.detail||skinproduct.details}</p>
                <h2>{skinproduct.price}$</h2>
                <button
                  onClick={()=>{
                    dispatch(addProduct(skinproduct))
                  }}
                  >Shop now</button>
                <img src={skinproduct.img} alt="" />
            </SwiperSlide>
            )
          })
        }
      </Swiper>
      </div>
    </div>
  )
}

export default Skinproduction