import './prodcart.css';
import {deleteproduct} from '../../rtk/Slices/ProductSlice'
import React from 'react'
import {useDispatch} from 'react-redux'
const Prodcar = ({cart}) => {
  const dispatch=useDispatch();
  return (
    <div className='prodcar'>
      <h2>{cart.name}</h2>
      <p>{cart.detail||cart.details}</p>
      <h4>{cart.price}$</h4>
      <img src={cart.img} alt="" />
      <button
      onClick={()=>{
        dispatch(deleteproduct(cart))
      }}
      >delete</button>
    </div>  
  )
}

export default Prodcar