import './cart.css';
import { addProduct, clear,deleteproduct} from '../rtk/Slices/ProductSlice';
import React from 'react'
import {useSelector,useDispatch} from 'react-redux'
import Prodcar from './Prodcart/Prodcar';
const Cart = () => {
  const dispatch=useDispatch();
  const cartProduct=useSelector((state)=>state.Products);
  return (
    <div className='cart'>
      <div className="container">
        <h1>
          <span>your Purchases</span>
          <button
          onClick={()=>{
            dispatch(clear())
          }}
          >clear</button>
        </h1>
        <div className="purchases">
          {
            cartProduct.map((prodcart,index)=>{
              return(
                <Prodcar cart={prodcart} key={index}/>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default Cart