import './vurtual.css';
import ReactCompareImage from 'react-compare-image-r18'
import React from 'react'
import { blueshadow ,heart,leftimage,rightimage} from '../Exportimage';
import {ProductsData} from '../data/Productsdata';
import { useState } from 'react';
import Product from './Product/Product';
const Virtual = () => {
  const [producttype,settype]=useState("");
  return (
    <div className='virtual'>
      <div className="virtual-top">
        <div className="left-virtual">
          <h1>Virtual Try-On</h1>
          <div>
            <span>Never Buy the wrong </span>
            <span>Shade Again!</span>
          </div>
          <span>Try Now!</span>
          <img src={blueshadow} alt="" />
        </div>
        <div className="right-virtual">
          <div className="wrapper">
            <ReactCompareImage 
              leftImage={leftimage}
              rightImage={rightimage}
            />
          </div>
        </div>
      </div>
      <div className="virtual-bottom">
        <img src={heart} alt="" />
        <h2>Our Feature Products</h2>
        <div className="features">
          <div className="sections">
            <button
            onClick={()=>{settype("")}}
            >All</button>
            <button
            onClick={()=>{settype("skin care")}}
            >Skin Care</button>
            <button
              onClick={()=>{settype("conditioner")}}            
            >Conditioner</button>
            <button
              onClick={()=>{settype("foundation")}}
            >Soundation</button>
          </div>
          <div className="feature-products">
            {
              ProductsData.filter((val)=>{
                if(producttype===""){
                  return val
                }
                else{
                  return val.type===producttype
                }
              }).map((PRODUCT,index)=>{
              return  <Product prod={PRODUCT} key={index}/>
              })
            }
            
          </div>
        </div>
      </div>
    </div>
  )
}

export default Virtual